import request from '/utils/request'
export default {
    name: 'SignupIndex',
    data(){
      return {
        user: {
          username: '',
          email: '',
          password: ''
        }
      }
    },
    methods: {
      async submit(){
        console.log('submit')
        const { data } = await request({
          method: 'POST',
          url: '/api/users',
          data: {
            user: this.user
          }
        })
        console.log('%c⧭', 'color: #ff0000', data)
        if(data){
          this.$router.replace('/')
        }

      }
    }

}
